import { Nav, Navbar, Container, Form, NavDropdown, Button, FormControl, } from 'react-bootstrap';
import '../styling/styling.css';



let navbar = document.querySelector(".navbar");
window.onscroll = () => {
    
  if (window.scrollY > 300) {
    
    console.log("gumagana");
    navbar.classList.add("bg-light");
    
  }
  else {
    
    console.log("alaws");
    navbar.classList.remove("bg-light");
  }

}




const NavBar = () => {


  
    return (
        <>
            <Navbar collapseOnSelect className = "sticky-top " bg = "dark" variant='dark' expand="lg"  >
                <Container className= "">
                    <Navbar.Brand className='bg-dark metalic-brand px-3 text-light' href="#home">ADS</Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        
                        <Nav className = "ms-auto">
                            <Nav.Link  className = "fw-bold " href="#abt">About Me</Nav.Link>
                            <Nav.Link  className = "fw-bold " eventKey={2} href="#projects">
                                Projects
                            </Nav.Link>
                            <Nav.Link  className = "fw-bold" eventKey={3} href="#snk">
                                Skillset
                            </Nav.Link>
                            <Nav.Link  className = "fw-bold " eventKey={4} href="#cm">
                                Contact Me
                            </Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </>

    );




}
export default NavBar